@import url(../../node_modules/normalize.css/normalize.css);
html,
body,
div,
span,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
address,
img,
small,
strong,
sub,
sup,
b,
i,
ol,
ul,
li,
form,
label,
article,
aside,
footer,
header,
nav,
section,
time,
audio,
video {
  margin: 0;
  outline: 0;
  border: 0;
  padding: 0;
  background: transparent;
}

html {
  word-break: normal;
}

body {
  min-height: 100vh;
  text-rendering: optimizeSpeed;
  scroll-behavior: smooth;
}

ul[class],
ol {
  list-style: none;
}

a {
  outline: 0;
  text-decoration: none;
  background: transparent;
  text-decoration-skip-ink: none;
}

address {
  font-style: normal;
}

input,
button,
textarea,
select {
  vertical-align: middle;
  margin: 0;
  border: none;
  border-radius: 0;
  padding: 0;
  font: inherit;
  color: inherit;
  background: transparent;
}

img {
  display: block;
  max-width: 100%;
}

:focus {
  outline: none;
}

body {
  font-family: "Roboto";
  font-weight: 300;
}

.title {
  font-family: "Roboto";
  font-weight: 400;
}

.preview__tab--active {
  font-family: "Roboto";
  font-weight: 700;
}

/* roboto-300 - latin_cyrillic-ext */
@font-face {
  font-family: "Roboto";
  font-weight: 300;
  font-style: normal;
  font-display: swap;
  src: local(""), url("../fonts/roboto-v20-latin_cyrillic-ext-300.woff2") format("woff2"), url("../fonts/roboto-v20-latin_cyrillic-ext-300.woff") format("woff");
  /* Chrome 6+, Firefox 3.6+, IE 9+, Safari 5.1+ */
}

/* roboto-regular - latin_cyrillic-ext */
@font-face {
  font-family: "Roboto";
  font-weight: 400;
  font-style: normal;
  font-display: swap;
  src: local(""), url("../fonts/roboto-v20-latin_cyrillic-ext-regular.woff2") format("woff2"), url("../fonts/roboto-v20-latin_cyrillic-ext-regular.woff") format("woff");
  /* Chrome 6+, Firefox 3.6+, IE 9+, Safari 5.1+ */
}

/* roboto-700 - latin_cyrillic-ext */
@font-face {
  font-family: "Roboto";
  font-weight: 700;
  font-style: normal;
  font-display: swap;
  src: local(""), url("../fonts/roboto-v20-latin_cyrillic-ext-700.woff2") format("woff2"), url("../fonts/roboto-v20-latin_cyrillic-ext-700.woff") format("woff");
  /* Chrome 6+, Firefox 3.6+, IE 9+, Safari 5.1+ */
}

html {
  box-sizing: border-box;
}

*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}

body {
  font-size: 16px;
  line-height: 1.5;
  color: #000;
}

.wrapper {
  display: flex;
  flex-direction: column;
  overflow: hidden;
}

.main {
  flex-grow: 1;
  background-color: #393e46;
}

.btn {
  display: flex;
  align-items: center;
  justify-content: center;
  outline: none;
  border: 1px solid rgba(0, 0, 0, 0.2);
  width: 220px;
  height: 65px;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.2);
  font-weight: 700;
  font-size: 18px;
  background-color: #fff;
  transition: background-color 0.4s;
  cursor: pointer;
}

.btn:hover {
  background-color: #ffd369;
}

.btn--white {
  color: #222831;
  background-color: #fff;
}

.btn--dark {
  border: none;
  color: #fff;
  background-color: #222831;
}

.btn--min {
  height: 50px;
}

.container {
  margin: 0 auto;
  padding: 0 15px;
  max-width: 1230px;
}

.title {
  font-size: 36px;
}

.show {
  display: block !important;
}

.hide {
  display: none;
}

.fade {
  animation: "fade" 1s;
}

