@keyframes up {
  0% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.3);
  }
  100% {
    transform: scale(1);
  }
}

@keyframes fade {
  0% {
    opacity: 0.1;
  }
  100% {
    opacity: 1;
  }
}

.header {
  color: #fff;
  background-color: #222831;
}

.header__top {
  display: flex;
  flex-direction: row-reverse;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 15px;
  border-bottom: 1px solid #ffd369;
  padding: 5px;
}

.header__contacts {
  position: relative;
  padding: 2px 10px;
  color: #000;
  background-color: #fff;
  transition: background-color 0.4s;
  cursor: pointer;
}

.header__contacts:hover {
  background-color: #ffd369;
}

.header__inner {
  position: absolute;
  left: 270px;
  display: none;
}

.header__list {
  display: flex;
  align-items: center;
  min-width: 470px;
}

.header__item {
  margin: 3px 10px;
}

.header__tel {
  color: #fff;
  transition: color 0.4s;
}

.header__tel:hover {
  color: #ffd369;
}

.header__content {
  display: flex;
  align-items: center;
  justify-content: space-between;
  min-width: 550px;
}

.header__logo {
  margin-right: 30px;
}

.header__icon {
  max-width: 90px;
  max-height: 70px;
  fill: #fff;
}

.header__links {
  display: flex;
  align-items: center;
  margin-right: auto;
}

.header__link {
  position: relative;
  z-index: 2;
  margin-right: 45px;
  font-weight: 700;
  font-size: 18px;
  color: #fff;
  transition: color 0.4s;
}

.header__link::after {
  background-position: center center;
  background-size: cover;
  background-repeat: no-repeat;
  content: "";
  position: absolute;
  display: block;
  left: -10px;
  bottom: 0;
  z-index: 1;
  width: 20px;
  height: 20px;
}

.header__link:hover {
  color: #ffd369;
}

.header__link:last-child {
  margin-right: 0;
}

.sidepanel {
  position: fixed;
  right: 30px;
  bottom: 20px;
  z-index: 10;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  width: 25px;
  height: 140px;
}

.sidepanel__icon {
  width: 34px;
  height: 34px;
  transition: transform 0.6s;
  cursor: pointer;
}

.sidepanel__icon:hover {
  transform: scale(1.05);
}

.preview {
  position: relative;
  padding: 28px 0 120px;
}

.preview__inner {
  display: flex;
  margin: 0 auto;
  width: 1130px;
  box-shadow: 0 4px 30px rgba(0, 0, 0, 0.25);
}

.preview__bg {
  position: absolute;
  right: 0;
  top: -155px;
  z-index: -1;
  width: 50vw;
  height: 900px;
  background: rgba(0, 0, 0, 0.15);
}

.preview__content {
  position: relative;
  max-width: 850px;
}

.preview__img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.preview__descr {
  position: absolute;
  left: -110px;
  top: 300px;
  padding: 50px;
  width: 550px;
  height: 359px;
  font-weight: 300;
  font-size: 24px;
  line-height: 36px;
  color: rgba(0, 0, 0, 0.7);
  background: rgba(251, 254, 93, 0.8);
}

.preview__tabs {
  padding: 35px 30px;
  width: 280px;
  background-color: #fff;
}

.preview__style {
  font-weight: 700;
  font-size: 16px;
}

.preview__nav {
  margin-top: 35px;
  border-left: 1px solid #000;
  padding-left: 26px;
}

.preview__tab {
  margin-top: 10px;
  font-size: 18px;
  color: rgba(0, 0, 0, 0.6);
  transition: 0.3s all;
  cursor: pointer;
}

.preview__tab--active {
  font-size: 22px;
  color: #000;
}

.offer {
  position: relative;
  padding: 70px 0 30px;
}

.offer__container {
  display: flex;
  justify-content: space-between;
  margin-bottom: 50px;
}

.offer__text {
  width: 580px;
}

.offer__descr {
  margin-top: 30px;
  font-weight: 300;
  font-size: 18px;
  line-height: 24px;
}

.offer__action {
  display: flex;
  align-items: center;
  justify-content: flex-end;
}

.offer__advantages {
  max-width: 330px;
}

.offer__advantages-text {
  margin-bottom: 20px;
  font-weight: 300;
  font-size: 18px;
  line-height: 21px;
}

.offer__subtitle {
  margin-bottom: 20px;
  font-weight: 700;
  font-size: 20px;
}

.offer__slider {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  max-width: 650px;
}

.offer__slider-counter {
  display: flex;
  align-items: center;
  justify-content: center;
  min-width: 100%;
  font-size: 24px;
  color: rgba(0, 0, 0, 0.5);
}

.offer__slider-wrapper {
  margin-bottom: 50px;
  min-width: 100%;
  box-shadow: 0 4px 30px rgba(0, 0, 0, 0.25);
}

.offer__slider-prev {
  margin-right: 20px;
  cursor: pointer;
}

.offer__slider-next {
  margin-left: 20px;
  cursor: pointer;
}

.offer__slider-icon {
  max-width: 45px;
  max-height: 45px;
}

.offer__slider-icon--rotate {
  transform: rotate(180deg);
}

.offer__slider #current {
  font-weight: 700;
  font-size: 48px;
  color: black;
}

.offer__slide {
  width: 100%;
  height: 390px;
}

.offer__img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.calculating {
  padding: 50px 0;
}

.calculating .title {
  margin-bottom: 50px;
  text-align: center;
}

.calculating__content {
  background-position: left;
  background-size: 48%;
  background-repeat: no-repeat;
}

.calculating__body {
  margin-left: auto;
  padding: 30px 0 40px;
  max-width: 50%;
  background: rgba(146, 242, 255, 0.1);
}

.calculating__subtitle {
  margin: 15px 0 20px;
  font-weight: 700;
  font-size: 18px;
  text-align: center;
}

.calculating__subtitle:first-child {
  margin-top: 0;
}

.calculating #gender::after {
  content: "";
  position: absolute;
  display: block;
  top: 50%;
  width: 16px;
  height: 16px;
  transform: translateY(-50%);
}

.calculating__choose {
  position: relative;
  max-width: 50%;
}

.calculating__choose-item {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 15px;
  outline: none;
  border: none;
  padding: 0 10px;
  min-width: 170px;
  min-height: 50px;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.2);
  font-size: 14px;
  text-align: center;
  background: #fff;
  transition: 0.3s all;
  cursor: pointer;
}

.calculating__choose-item--active {
  color: #fff;
  background-color: #393e46;
}

.calculating__choose--top {
  display: flex;
  align-items: center;
  justify-content: center;
  max-width: 100%;
}

.calculating__box {
  display: flex;
  align-items: center;
  justify-content: center;
}

.calculating__divider {
  margin: 40px auto;
  width: 490px;
  height: 1px;
  background-color: rgba(0, 0, 0, 0.2);
}

.calculating__total {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin: 0 auto;
  width: 490px;
}

.calculating__result {
  font-weight: 700;
  font-size: 18px;
}

.calculating__result span {
  font-size: 42px;
}

.menu {
  padding: 40px 0 50px;
}

.menu .container {
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
}

.menu__title {
  text-align: center;
}

.menu__body {
  margin-top: 50px;
  padding: 50px 0;
  width: 100%;
  background: rgba(146, 242, 255, 0.1);
}

.menu__item {
  display: flex;
  flex-direction: column;
  width: 320px;
  min-height: 450px;
  box-shadow: 0 4px 25px rgba(0, 0, 0, 0.25);
  font-weight: 300;
  font-size: 16px;
  background: #fff;
}

.menu__item img {
  width: 100%;
  height: 200px;
  object-fit: cover;
}

.menu__item-subtitle {
  margin-top: 20px;
  padding: 0 20px;
  font-weight: 700;
  font-size: 18px;
}

.menu__item-descr {
  flex: 1 1 auto;
  margin-top: 20px;
  padding: 0 20px;
}

.menu__item-divider {
  margin-top: 40px;
  width: 100%;
  height: 1px;
  background-color: rgba(0, 0, 0, 0.25);
}

.menu__item-price {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 10px 20px;
}

.menu__item-price span {
  font-weight: 700;
  font-size: 24px;
}

.order {
  padding-bottom: 80px;
}

.order .title {
  text-align: center;
}

.order__form {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-top: 70px;
  padding: 0 100px;
}

.order__form img {
  transform: scale(1.5);
}

.order__input {
  outline: none;
  border: none;
  padding: 0 20px;
  width: 280px;
  height: 50px;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.2);
  font-size: 18px;
  text-align: center;
  background: #fff;
}

.promotion {
  position: relative;
  padding: 70px 0 240px;
}

.promotion .container {
  display: flex;
}

.promotion .bgc_y {
  position: absolute;
  left: 0;
  top: -50px;
  z-index: -1;
  width: 50%;
  height: 499px;
  background: rgba(243, 255, 222, 0.35);
}

.promotion__text {
  width: 50%;
}

.promotion__descr {
  margin-top: 40px;
  font-weight: 300;
  font-size: 20px;
  line-height: 24px;
}

.promotion__descr span {
  font-weight: 700;
  font-size: 26px;
}

.promotion__timer {
  width: 50%;
}

.promotion__timer .title {
  text-align: right;
}

.promotion__timer .timer {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-top: 60px;
  padding-left: 95px;
}

.promotion__timer .timer__block {
  width: 102px;
  height: 120px;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.25);
  font-weight: 300;
  font-size: 16px;
  text-align: center;
  background: #fff;
}

.promotion__timer .timer__block span {
  display: block;
  margin-top: 20px;
  margin-bottom: 5px;
  font-weight: 700;
  font-size: 56px;
}

.footer {
  padding: 45px 0 50px;
  min-height: 180px;
  color: #fff;
  background-color: #222831;
}

.footer__container {
  display: flex;
  align-items: flex-end;
  justify-content: space-between;
  height: 100%;
}

.footer__subtitle {
  font-size: 20px;
}

.footer__link {
  display: block;
  margin-top: 15px;
  font-size: 16px;
  color: #fff;
  transition: color 0.4s;
}

.footer__link:hover {
  color: #ffd369;
}

.footer__img {
  max-width: 80px;
  max-height: 80px;
}

.footer__numbers {
  text-align: right;
}

.modal {
  position: fixed;
  left: 0;
  top: 0;
  z-index: 1050;
  display: none;
  overflow: hidden;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
}

.modal__dialog {
  margin: 40px auto;
  max-width: 500px;
}

.modal__content {
  position: relative;
  overflow-y: auto;
  border: 1px solid rgba(0, 0, 0, 0.2);
  border-radius: 4px;
  padding: 40px;
  width: 100%;
  max-height: 80vh;
  background-color: #fff;
}

.modal__close {
  position: absolute;
  right: 14px;
  top: 8px;
  border: none;
  font-weight: 700;
  font-size: 30px;
  color: #000;
  background-color: transparent;
  opacity: 0.5;
  cursor: pointer;
}

.modal__title {
  font-size: 22px;
  text-align: center;
  text-transform: uppercase;
}

.modal__input {
  display: block;
  margin: 20px auto;
  outline: none;
  border: none;
  padding: 0 20px;
  width: 280px;
  height: 50px;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.2);
  font-size: 18px;
  text-align: center;
  background: #fff;
}

.modal .btn {
  display: block;
  margin: 0 auto;
  width: 280px;
}
